import React, { useState, useEffect } from 'react';
import { getComments, postComment } from '../request/request';
import CommentItem from '../components/CommentItem';
import Loading from '../components/Loading';

const Comments = (props) => {
	const [ data, setData ] = useState([]);
	const [ isLoading, setLoading ] = useState(false);
	const [ commentInput, setComment ] = useState('');
	const [ isPostLoading, setPostLoading ] = useState(false);

	useEffect(
		() => {
			function fetchComments() {
				getComments(props.parentId)
					.then((res) => {
						setData(res);
						setLoading(false);
					})
					.catch((err) => {
						setLoading(false);
						console.log(err);
					});
			}
			setLoading(true);
			fetchComments();
			const interval = setInterval(() => fetchComments(), 45000);
			return () => clearInterval(interval);
		},
		[ props.parentId ]
	);

	function handleNewComment(e) {
		if (!isPostLoading) {
			setPostLoading(true);
			e.preventDefault();
			postComment(props.parentId, commentInput).then((res) => {
				// console.log(res);
				setData([ ...data, res ]);
				setComment('');
				setPostLoading(false);
			});
		}
	}

	if (isLoading) {
		return <Loading />;
	}

	return (
		<div className="mb-40 pt-10">
			<h3 className="ui dividing header ellipsis pb-10">{props.title}</h3>
			{!data || data.length === 0 ? (
				<p className="pb-20">There's no comment</p>
			) : (
				<div className="ui comments">{data.map((item) => <CommentItem data={item} key={item._id} />)}</div>
			)}

			<form className="ui reply form" id="comment-form" onSubmit={(e) => handleNewComment(e)}>
				<div className="field">
					<textarea
						id="comment-box"
						rows="5"
						onChange={(e) => setComment(e.target.value)}
						value={commentInput}
					/>
				</div>
				<button className="ui blue labeled submit icon button">
					<i className="icon edit" /> Add Reply
				</button>
				{isPostLoading && <span> Processing</span>}
			</form>
		</div>
	);
};

export default Comments;
