import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
// import { getSearch } from '../request/request';

const AppBar = () => {
	const [ input, setInput ] = useState('');
	let history = useHistory();

	function handleSearch(e) {
		e.preventDefault();
		history.push(`/search/${input}`);
	}

	return (
		<div id="app-bar">
			<div className="ui container small secondary menu">
				<span className="item active cursor-pointer" onClick={() => history.goBack()}>
					H
				</span>
				<div className="item" id="search-bar">
					<form action="/search/" method="GET" className="ui icon input" onSubmit={(e) => handleSearch(e)}>
						<input
							type="text"
							name="q"
							placeholder="Search anime titles..."
							value={input}
							onChange={(e) => setInput(e.target.value)}
						/>
						<i className="search link icon" />
					</form>
				</div>
			</div>
		</div>
	);
};

export default AppBar;
