import React from 'react';
import { Link } from 'react-router-dom';
import moment from 'moment';

const GridItem = (props) => {
	if (!props.data.img && !props.data.Show) {
		return <div />;
	}

	return (
		<div className="grid-item">
			<Link className="jumper" to={props.data.href ? props.data.href : '/' + props.data.Episode.uri} />
			{props.data.releasedAt && (
				<div className="time">
					<p>{moment(props.data.releasedAt).fromNow()}</p>
				</div>
			)}

			<div className="ui placeholder">
				<div className="image">
					<img src={props.data.img || props.data.Show.thumbnailUrl} alt="grid-cover" />
				</div>
			</div>
			<div className="pl-5 mt-5">
				<p className="f-bold ellipsis">{props.data.parentId || props.data.title}</p>
				<p className="fs-0 ellipsis">{props.data.episodes || props.data.title}</p>
			</div>
		</div>
	);
};

export default GridItem;
