import React from 'react';
import { Link } from 'react-router-dom';

const GridWrap = (props) => {
	return (
		<div className="grid-wrap">
			{props.data && props.data.map((item) => <GridWrapItem data={item} key={item.href} />)}
		</div>
	);
};

const GridWrapItem = (props) => (
	<div className="wrap-item">
		<div className="ui placeholder h-100">
			<div className="image h-100">
				<img src={props.data.img} alt="cover" height="300" />
			</div>
		</div>
		<Link className="wrap-info" to={props.data.href}>
			<div>
				<p className="f-bold ellipsis">{props.data.title}</p>
				<p className="ellipsis">{props.data.episodes}</p>
				<p className="ellipsis">{props.data.status.replace('Status : ', '')}</p>
			</div>
		</Link>
	</div>
);

export default GridWrap;
