import React from 'react';
import GridItem from './GridItem';
import Loading from './Loading';

const GridList = (props) => {
	let grid = React.createRef();

	function scrollBooks(direction) {
		if (direction === 'right') {
			grid.current.scrollLeft += 400;
		} else {
			grid.current.scrollLeft -= 400;
		}
	}
	return (
		<div className="grid-list">
			<div className="float-right control hide-xs">
				<span onClick={() => scrollBooks('left')}>‹</span>
				<span onClick={() => scrollBooks('right')}>›</span>
			</div>
			<h3 className="title">{props.title}</h3>

			{!props.data || props.data.length === 0 ? (
				<div className="pt-20">
					<Loading />
				</div>
			) : (
				<div className="grid-container" ref={grid}>
					{props.data.map((item) => <GridItem key={item.id || item._id || item.title} data={item} />)}
					<div className="grid-spacer">
						<img src="ic_launcher48.png" alt="spacer" />
					</div>
				</div>
			)}
		</div>
	);
};

export default GridList;
