import axios from 'axios';

export const url = '';

export async function getHome(key) {
	try {
		let { data } = await axios.get(`${url}/api/home?json=true&${key}`);
		return data;
	} catch (err) {
		console.log(err);
	}
}

export async function getFields() {
	try {
		let { data } = await axios.get(`${url}/api/stats/home/new`);
		return data;
	} catch (err) {
		console.log(err);
	}
}

export async function postNewData(body) {
	try {
		let { data } = await axios.post(`${url}/api/stats/home/new`, body);
		return data;
	} catch (err) {
		console.log(err);
	}
}

export async function getDetail(id) {
	try {
		let { data } = await axios.get(`${url}/api/episodes/${id}?json=true`);
		return data.data;
	} catch (err) {
		console.log(err);
	}
}

export async function getSearch(query) {
	try {
		let { data } = await axios.get(`${url}/api/search?json=true&q=${query}`);
		return data.data;
	} catch (err) {
		console.log(err);
	}
}

export async function getSeries(id) {
	try {
		let { data } = await axios.get(`${url}/api/series/${id}?json=true`);
		return data.data;
	} catch (err) {
		console.log(err);
	}
}

export async function getStats(key) {
	try {
		let { data } = await axios.get(`${url}/api/stats/${key}`);
		return data;
	} catch (err) {
		console.log(err);
	}
}

export async function deleteData(type, key) {
	try {
		let { data } = await axios.delete(`${url}/api/stats/${type}?${key}`);
		return data.data;
	} catch (err) {
		console.log(err);
	}
}

export async function getComments(parentId) {
	try {
		let { data } = await axios.get(`${url}/api/comments/${parentId}`);
		return data.comments;
	} catch (err) {
		console.log(err);
	}
}

export async function postComment(parentId, text) {
	let authorId = localStorage.getItem('authorId');

	if (!authorId) authorId = localStorage.setItem('authorId', Date.now());
	let body = {
		authorId,
		parentId,
		text
	};
	try {
		let { data } = await axios.post(`${url}/api/comments/${parentId}`, body);
		return data.comment;
	} catch (err) {
		console.log(err);
	}
}
