import React, { useState, useEffect } from 'react';
import { Link, useParams, Redirect } from 'react-router-dom';
import { getDetail, url } from '../request/request';
import Loading from '../components/Loading';
import Comments from './Comments';

const Detail = () => {
	const [ data, setData ] = useState({});
	const [ isLoading, setLoading ] = useState(true);
	const { episodeId } = useParams();

	useEffect(
		() => {
			setLoading(true);
			let epData = localStorage.getItem(episodeId);
			if (epData) {
				epData = JSON.parse(epData);
				setData(epData);
				setLoading(false);
			} else {
				getDetail(episodeId)
					.then((res) => {
						setData(res);
						setLoading(false);
						console.log(res);
						epData = res;
						document.title = `Watch ${res.title} for free`;
					})
					.catch((err) => {
						setLoading(false);
						console.log(err);
					});
			}
			return () => {
				if (epData) localStorage.setItem(episodeId, JSON.stringify(epData));
			};
		},
		[ episodeId ]
	);

	if (isLoading) {
		return <Loading />;
	}

	if (!isLoading && !data.title) {
		return <Redirect to="/" />;
	}

	return (
		<div>
			<div className="ui container">
				<div className="full fixed-video">
					<div className="video-container">
						{data.video && (
							<video
								src={`${url}/api/video?url=${data.video}&embed=${data.embed}`}
								width="100%"
								height="100%"
								controls
							/>
						)}
					</div>
					<div className="ui my-10 expanded-y">
						<Link to={data.prev}>
							<i className="left chevron icon" />
							Prev
						</Link>
						<Link to={data.all}>All Episodes</Link>
						<Link to={data.next}>
							Next
							<i className="right chevron icon" />
						</Link>
					</div>
					<div className="m-1 comment-section">
						{episodeId && !isLoading && <Comments parentId={episodeId} title={data.title} />}
					</div>
				</div>
			</div>
		</div>
	);
};

export default Detail;
