import React, { useState, useEffect } from 'react';
import { getFields, postNewData } from '../request/request';
import Loading from '../components/Loading';

const NewData = () => {
	const [ inputs, setInputs ] = useState({});
	const [ isLoading, setLoading ] = useState(false);
	const [ fields, setFields ] = useState([]);
	const [ isProcessing, setProcessing ] = useState(false);

	useEffect(() => {
		setLoading(true);
		getFields().then((res) => {
			setFields(res.data);
			setLoading(false);
		});
	}, []);

	const handleChange = (e) => {
		const { name, value } = e.target;
		setInputs({ ...inputs, [name]: value });
	};

	function handleSubmit(e) {
		setProcessing(true);
		e.preventDefault();
		if (!isProcessing) {
			let clearInputs = {};

			console.log(inputs);
			// if (allowSubmit) {
			postNewData(inputs).then((res) => {
				if (res) {
					setInputs(clearInputs);
				}
				setProcessing(false);
			});
			// }
		}
	}

	if (isLoading) return <Loading />;

	return (
		<div className="ui container mb-40">
			{isProcessing && <p className="fixed-right">(processing...)</p>}

			<h3 className="ui dividing header">Add a new home content</h3>

			<form className="ui form" onSubmit={(e) => handleSubmit(e)}>
				{fields &&
					fields.map((field) => (
						<div key={field} className="field">
							<label className="capitalize">{field}</label>
							<textarea
								type="text"
								value={inputs[field] || ''}
								name={field}
								rows="3"
								onChange={(e) => handleChange(e)}
							/>
						</div>
					))}
				<button className="ui button" type="submit">
					Submit
				</button>
			</form>
		</div>
	);
};

export default NewData;
