import React, { useState, useEffect } from 'react';
import { getSearch } from '../request/request';
import { useParams } from 'react-router-dom';
import GridWrap from '../components/GridWrap';
import Loading from '../components/Loading';

const Search = () => {
	const [ data, setData ] = useState([]);
	const [ isLoading, setLoading ] = useState(true);
	const { searchKey } = useParams();

	useEffect(
		() => {
			setLoading(true);
			getSearch(searchKey)
				.then((res) => {
					setData(res);
					setLoading(false);
					document.title = 'results for: ' + searchKey;
				})
				.catch((err) => {
					console.log(err);
					setLoading(false);
				});
		},
		[ searchKey ]
	);

	return (
		<div>
			<div className="ui container pb-10">
				<h3>results for: {searchKey}</h3>
			</div>

			{!isLoading && data && data.length === 0 && <p className="text-center pt-10">There's no result</p>}
			{isLoading ? <Loading /> : <GridWrap data={data} />}
		</div>
	);
};

export default Search;
