import React, { useState, useEffect } from 'react';
import ItemList from '../components/ItemLists';
import { getSeries } from '../request/request';
import { useParams, Redirect } from 'react-router-dom';
import Loading from '../components/Loading';

const Series = () => {
	const [ data, setData ] = useState({});
	const [ isLoading, setLoading ] = useState(true);
	const { seriesId } = useParams();
	useEffect(
		() => {
			setLoading(true);
			getSeries(seriesId)
				.then((res) => {
					setData(res);
					document.title = `Watch ${res.title} for free without ads`;
					setLoading(false);
				})
				.catch((err) => {
					console.log(err);
				});
		},
		[ seriesId ]
	);

	if (!isLoading && !data.title) {
		return <Redirect to="/" />;
	}

	if (isLoading) {
		return <Loading />;
	}
	return (
		<div>
			<div className="ui container mb-40">
				<div className="ui grid">
					<div className="large screen only four wide column">
						<img className="series-img" src={data.img} alt="cover" />
					</div>
					<div className="large screen only twelve wide column">
						<h1>{data.title}</h1>
						<p>{data.status}</p>
						<p>{data.desc}</p>
					</div>
					<div className="mobile only sixteen wide column">
						<div className="series-img-m ui placeholder">
							<div className="ui image">
								<img width="100%" height="200" src={data.img} alt="cover" />
							</div>
						</div>
						<h1>{data.title}</h1>
						<p>{data.status}</p>
						<p>{data.desc}</p>
					</div>
				</div>

				{data && data.episodes && <ItemList data={data.episodes} />}
			</div>
		</div>
	);
};

export default Series;
