import React, { useState, useEffect } from 'react';
import { getStats, deleteData } from '../request/request';
import moment from 'moment';
import Loading from '../components/Loading';
import { Redirect, useParams, Link } from 'react-router-dom';

const Stats = () => {
	const [ stats, setStats ] = useState([]);
	const [ isLoading, setLoading ] = useState(false);
	const { type } = useParams();
	const [ isProcessing, setProcessing ] = useState(false);

	useEffect(
		() => {
			setLoading(true);
			getStats(type)
				.then((res) => {
					setStats(res.data);
					setLoading(false);
				})
				.catch((err) => {
					console.log(err);
					setLoading(false);
				});
		},
		[ type ]
	);

	if (!isLoading & !stats) {
		return <Redirect to="/" />;
	}

	function handleDelete(type, key, value) {
		setProcessing(true);
		deleteData(type, `${key}=${value}`)
			.then((res) => {
				if (res.deletedCount > 0) {
					setStats(stats.filter((item) => item._id !== value));
				}
				setProcessing(false);
			})
			.catch(() => setProcessing(false));
	}

	return (
		<div className="ui container mb-40">
			{isProcessing && <p className="fixed-right">(processing...)</p>}

			<div className="five ui buttons pb-20">
				<Link to="log" className="ui button">
					Log
				</Link>
				<Link to="comment" className="ui button">
					Comment
				</Link>
				<Link to="episode" className="ui button">
					Episode
				</Link>
				<Link to="series" className="ui button">
					Seires
				</Link>
				<Link to="home" className="ui button">
					Home
				</Link>
			</div>
			{isLoading ? (
				<Loading />
			) : (
				stats && (
					<div>
						<h3>stats: {type}</h3>
						<ol className="ui list">
							{stats.map((item) => (
								<li key={item._id}>
									{item._id}

									<span
										onClick={() => handleDelete(type, '_id', item._id)}
										className="float-right cursor-pointer"
									>
										del
									</span>
									<ol>
										{Object.keys(item).map((key, i) => {
											if (key !== '_id') {
												if (key.includes('At')) {
													return (
														<li key={i}>
															{key}: {moment(item[key]).fromNow()}
														</li>
													);
												} else {
													return (
														<li key={i}>
															{key}: {JSON.stringify(item[key])}
														</li>
													);
												}
											} else {
												return <span key={i} />;
											}
										})}
									</ol>
								</li>
							))}
						</ol>
					</div>
				)
			)}
		</div>
	);
};

export default Stats;
